a{
  cursor: pointer;
}
.App {
  overflow-x: hidden;
  scroll-behavior: smooth;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background-color: rgba(80, 80, 80, 0);
  color: white;
  background-image: url(./imgs/toronto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.App-header {
  background-image: url(./imgs/toronto.jpg);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.banner-content{
  margin-left: calc(1vw - 2px);
}
.cal-month{
  text-align: center;
  height: 23px;
  width: 50px;
  border: 1px solid black;
  background-color: brown;
  color: white;
  
}
.cal-day{
  text-align: center;
  height: 23px;
  width: 50px;
  border: 1px solid black;
}
#date{
  width: 100px;
  text-align: left;
  padding-right: 30px;
}
.dates-container{
  width: 800px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}
.dashboard{
  width: 100%;
  background-color: black;
  height: 50px;
}
#event{
  height: 100%;
  margin: 0;
  padding: 5px 0 5px 0;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: space-evenly;
  transition: 0.7s;
}
#event:hover{
  /*text-decoration: underline;*/
  cursor: pointer;
}
.event-container{
  padding-top: 10px;
}
.event-header{
  width: 800px;
  font-weight: 500;
  display: flex;
  flex-direction: row;
}
.event-header:hover{
  text-decoration: underline;
}
.event-info{
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 700px;
  font-size: 20px;
}
#herman-picture{
  background-image: url(./imgs/trees.jpg);
  width: 300px;
  height: 300px;
  background-position: center;
  background-size: 90%;
  border-radius: 100%;
  box-shadow: 5px 7px 15px 2px  #4e4e4e;
}
.herman-info-box{
  width: 680px;
  margin-left: 50px;
}
img{
  content: url(./imgs/vector.png);
}
.intro{
  width: 1000px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-evenly;
  padding: 20px;
  transition: 0.7s;
}

.intro-box{
  background-color: rgba(255, 255, 255);
  height: auto;
  width: 100%;
  color: rgba(100, 100, 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 5px 70px 150px 2px  #4e4e4e;
  border-top: 5px solid #4e4e4e;
}
.Footer{
  width: 100%;
  height: 500px;
  background-color: black;
}
#month{
  font-size: 35px;
  font-weight: 700;
}
.month-container{
  width: 900px;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding-top: 40px;
}
#photo-captions{
  margin-top: 20vh;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.61);
}
.projects{
  width: 1000px;
}
.projects-box{
  min-height: 40vh;
  width: 100%;
}
.reflection{
  width: 800px;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  animation: slide-down 1.8s ease;
}
.reflection-content{
  width: 600px;
  height: auto;
  padding-right: 3vw;
  padding-top: 2vh;
  padding-bottom: 2vh;
  animation: slide-down 1.8s ease;
}
.section-banner{
  width: 300px;
  height: 80px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}

#subtitle{
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(15px + 1vmin);
  font-weight: 700;
}
.spacer{
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 75vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-bottom: 5px solid #4e4e4e;
}
.spacer-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.time-container{
  width: 1000px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  background-color: white;
  transition: 0.7s;
  margin-bottom: 12vh;
}

.timeline{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-top: 5px solid #4e4e4e;
  border-bottom: 5px solid #4e4e4e;
}

#time-slot{
  width: 100%;
  height: 100%;
  color: rgb(100, 100, 100);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
#time-slot:hover{
  color: rgba(0, 0, 0, 0.89);
  border: 2 solid grey
}

#title{
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  font-size: calc(45px + 1vmin);
  font-weight: 700;
  padding-bottom: 15px;
}

.title-card{
  background-color: rgba(0, 0, 0, 0.7);
  width: 100%;
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 20px;
  position: relative;
}

ul {
  list-style-type: square;
  list-style-position: outside;
  list-style-image: none;
}

@media (max-width: 500px) {
  .cal-month{
    height: 4vw;
    width: 8vw;
  }
  .cal-day{
    height: 4vw;
    width: 8vw;
  }
  #date{
    font-size: calc(5px + 2vmin);
  }
  #event-info{
    font-size: calc(10px + 2vmin);
  }
  #month{
    font-size: calc(25px + 1vmin);
    font-weight: 700;
  }
  .month-container{
    width: 100%;
    justify-content: center;
  }
  #subtitle{
  font-size: calc(10px + 1vmin);
  justify-content: flex-start;
  }
  #title{
    font-size: calc(25px + 1vmin);
    text-align: center;
  }
  .title-card{
    height: auto;
  }
}
@media (min-width: 501px) and (max-width: 999px){
  .month-container{
    margin-left: 10vw;
  }
}
@media (max-width: 999px) {
  .App-header{
    background-position: -270px;
  }
  .about-box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 3vh;
  }
  .banner-content{
    margin-left: 0;
  }
  #date{
    width: 10%;
    text-align: left;
    padding-right: 30px;
  }
  .dates-container{
    width: 80%;
  }
  .event-header{
    width: 90%;
    font-weight: 500;
    display: flex;
    flex-direction: row;
  }
  .event-info{
    width: 90%;
    font-size: 20px;
  }
  #herman-picture{
    width: 250px;
    height: 250px;
    box-shadow: 2px 5px 10px 1px  #4e4e4e;
  }
  .herman-info-box{
    width: 90%;
    margin-left: 0px;
  }
  .intro{
    
    width: 100%;
    padding: 0.2vw, 0, 100px, 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: rgb(130, 130, 130);
    background-color: rgba(255, 255, 255, 0.91);
  }
  .intro-box{
    background-image: url(./imgs/toronto.jpg);
    background-attachment: fixed;
    background-position: -270px;
    background-repeat: no-repeat;
    background-size: cover;
    height: auto;
    width: 100%;
    color: rgba(150, 150, 150, 0.856);
    transition: 0.7s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 0px;
    padding-bottom: 0px;
  }
  .section-banner{
    width: 300px;
    height: 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
  .spacer{
    background-position: -270px;
  }
  .time-container{
    width: 100%;
    background-image: url(./imgs/toronto.jpg);
    background-attachment: fixed;
    background-position: -270px;
    background-repeat: no-repeat;
    background-size: cover;
    margin-bottom: 10vw;
  }
  #time-slot{
    background-color: rgba(255, 255, 255, 0.93);
    width: 100%;
  }
  .month-container{
    width: 100%;
  }
  .reflection{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    animation: slide-down 1.8s ease;
  }
  .reflection-content{
    width: 100%;
    height: auto;
    padding-right: 3vw;
    padding-top: 2vh;
    padding-bottom: 2vh;
    animation: slide-down 1.8s ease;
  }
}

